import axios from 'axios';
import React from 'react';
import {withRouter} from 'react-router-dom';
import {Container, Table, TableRow, TableHeading, TableData, Button} from './ContactDisplayElements'

const OrderView = (props) => {
    const deleteContact =(id) => {
        axios.delete(`/api/contactslist/${id}`);
    }

    const renderTable = ({orderdata}) => {
        if(orderdata){
            return orderdata.map((item) => {
                return(
                    <TableRow>
                        <TableData>{item.order_id}</TableData>
                        <TableData>{item.name}</TableData>
                        <TableData>{item.email}</TableData>
                        <TableData>{item.message}</TableData>
                        <TableData>{item.date}</TableData>
                        <TableData><Button onClick={() => deleteContact(item._id)}>Delete</Button></TableData>
                    </TableRow>
                )
            })
        }
    }



    return(
    
        <Container style={{overflowX:'auto'}}>
            <center><h3>Message Details</h3></center>
            <Table className="table table-responsive">
                <thead>
                    <TableRow>
                        <TableHeading>ID</TableHeading>
                        <TableHeading>Name</TableHeading>
                        <TableHeading>Email Address</TableHeading>
                        <TableHeading>Message</TableHeading>
                        <TableHeading>Date</TableHeading>
                        <TableHeading>Delete</TableHeading>
                    </TableRow>
                </thead>
                <tbody>
                    {renderTable(props)}
                </tbody>
            </Table>
        </Container>
    )
}

export default withRouter(OrderView);