import React,{Component} from 'react';
import axios from 'axios';
import Employeedb from './index';

const employeeurl = "/api/employeeslist";


class Employee extends Component{
    constructor(){
        super()

        this.state={
            loading: false,
            orders:''
        }
    }

    render(){
        console.log(this.props)
        if (!this.state.orders){
            return <center><img src="https://media.giphy.com/media/kUTME7ABmhYg5J3psM/giphy.gif" alt="loader-gif" border="0"/></center>
        }
        return(
            <div className="container">
                <Employeedb orderdata={this.state.orders}/>
            </div>
            
        )
    }


    /* async componentDidMount(){
        let response =  await axios.get(`${employeeurl}`)
        this.setState({orders:response.data})
    } */


    componentDidMount(){
        axios.get(`${employeeurl}`)
        .then((response) =>  {this.setState({orders:response.data})})
    }

}

export default Employee;