import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';

import Employees from './Employess/EmployessApi';
import ShowContacts from './ContactDisplay/ContactApi';
import Cards from './Cards/Cards';
import SideBar from './SideBar/SideBar';



const Dashboard =() => {

    return(

        <BrowserRouter>
            <SideBar />
            <div>
                <Route exact path="/dashboard" component={Cards}></Route>
                <Route exact path="/dashboard/employees" component={Employees}></Route>
                <Route exact path="/dashboard/messages" component={ShowContacts}></Route>
            </div>
        </BrowserRouter>
       )
}

export default Dashboard;
