import React,{Component} from 'react';
import {AddressContainer, 
    AddressH1, 
    AddressWrapper, 
    AddressP, 
    AddressIcon, 
    AddressH2, 
    AddressCard, 
    AddressEmail, 
    AddressPhone} from './AddressElements';

    import {
        ContactContainer,
        FormContent,
        Form,
        FormH1,
        FormLabel,
        FormInput,
        FormTextarea,
        FormButton
        } from './ContactElements';

import Icon1 from '../../images/usa.jpg'
import Icon2 from '../../images/IT-HUB.jpg'
import Icon3 from '../../images/getintouch.jpg'




const instercontact = "/api/contacts";

class ContactSection extends Component{
    constructor(){
        super()
        this.state={
            order_id: Math.floor(Math.random() * (+1000 - +10)) + +10,
            name:'',
            phone:'',
            email:''
        }

    }

    handleChangeName = (event) => {
        this.setState({name:event.target.value})
    }

    handleChangeEmail =(event) => {
        this.setState({email:event.target.value})
    }

    handleChangeMessage =(event) => {
        this.setState({message:event.target.value})
    }

    handleSubmit = () => {
       fetch(instercontact,{
           method:'POST',
           headers:{
               'Accept':'application/json',
               'Content-Type':'application/json'
           },
           body:JSON.stringify(this.state)
       })
       alert("Thanks for Contacting Us");

    }
    

render(){
    console.log(this.props)
    return(
        <>
        <div id="contact">
            <AddressContainer >
                <AddressH1>Contact Us</AddressH1>
                <AddressWrapper>
                    <AddressCard>
                        <AddressIcon src={Icon1} />
                        <AddressH2>Headquaters - USA</AddressH2>
                        <AddressP>205 Van Buren Street Suite 120 Herndon VA20170</AddressP>
                        <AddressEmail>Email: info@solvency.us</AddressEmail>
                        <AddressEmail>Sales: sales@solvency.us</AddressEmail>
                        <AddressEmail>Career: career@solvency.us</AddressEmail>
                    </AddressCard>
                    <AddressCard>
                        <AddressIcon src={Icon2} />
                        <AddressH2>Branch Office - India - Khammam</AddressH2>
                        <AddressP>IT HUB, 5th Floor, Block-B,  Indira Nagar Colony, Khammam, Telangana 507002 India</AddressP>
                        <AddressPhone>Ph: 9493316846</AddressPhone>
                    </AddressCard>
                    <AddressCard>
                        <AddressIcon src={Icon3} />
                        <AddressH2>Branch Office - India - Hyderabad</AddressH2>
                        <AddressP>Street no 1, Kakathiya Nagar, Habsiguda, Hyderabad - 500007, Telangana India</AddressP>
                        <AddressPhone>Ph: 9849661686</AddressPhone>
                    </AddressCard>
                </AddressWrapper>
            </AddressContainer>
            <ContactContainer>
                <FormContent>
                    <Form action="#" autoComplete="off">
                        <FormH1>Message Us</FormH1>
                        <FormLabel htmlFor='for' >Name</FormLabel>
                        <FormInput type='text' required name="name" placeholder="Please Enter Your Name"
                            className="form-control" onChange={this.handleChangeName} />
                        <FormLabel htmlFor='for'>Email</FormLabel>
                        <FormInput type='email' required name="email" placeholder="Please Enter Your Email" 
                            className="form-control" onChange={this.handleChangeEmail}/>
                        <FormLabel htmlFor='for'>Message</FormLabel>
                        <FormTextarea type='textarea' required name="message" placeholder="Message Us"
                            className="form-control" onChange={this.handleChangeMessage}/>
                        <FormButton type='submit' onClick={this.handleSubmit}>Send</FormButton>
                    </Form>
                </FormContent>
            </ContactContainer>
        </div>
        </>
    )
}
}

export default ContactSection;