import React from 'react';
import {animateScroll as scroll} from 'react-scroll';
import {FooterContainer, Footer, FooterH2} from './FooterEelements';

const FooterSection = () => {
    
    const toggleHome = () =>{
        scroll.scrollToTop();
    }

    return(
        <>
        <FooterContainer>
            <Footer>
                <FooterH2 to='/' onClick={toggleHome}>Copyrights © 2015 Solvency Inc, All rights reserved.</FooterH2>
            </Footer>
        </FooterContainer>
        </>
    )
}

export default FooterSection;