import Zoom from 'react-reveal/Zoom';
import React from 'react';
import { Button } from '../ButtonElement';
import {InfoContainer, InfoWrapper, InfoRow, Column1, Column2, TextWrapper, TopLine, Heading, Subtitle, BtnWrap, Img, ImgWrap } from './InfoElements';

const InfoSection = ({lightBg, imgStart, topLine, lightText, darkText, description, buttonLable, headLine, img, alt, id, description2, description3,desclLine1, desclLine2, desclLine3, desclLine4}) => {
    return (
        <>
            <InfoContainer lightBg={lightBg} id={id} >
                <InfoWrapper>
                    <InfoRow imgStart={imgStart}>
                        <Column1>
                            <TextWrapper>
                                <TopLine>{topLine}</TopLine>
                                <Heading lightText={lightText}>{headLine}</Heading>
                                <Subtitle darkText={darkText}>{description}<br/>{description2}<br/>{description3}<br/>{desclLine1}<br/>{desclLine2}<br/>{desclLine3}<br/>{desclLine4}</Subtitle>
                                <BtnWrap>
                                    <Button to='/' primary='true' dark='true' smooth={true} duration={500} spy={true} exact="true"
                                    offset={-80}>{buttonLable}</Button>
                                </BtnWrap>
                            </TextWrapper>
                        </Column1>
                        <Column2>
                            <ImgWrap>
                                <Zoom>
                                    <Img src={img} alt={alt}/>
                                </Zoom>
                            </ImgWrap>
                        </Column2>
                    </InfoRow>
                </InfoWrapper>
            </InfoContainer>
        </>
    )
}

export default InfoSection
