import axios from 'axios';
import React, {useState } from 'react';
import { setUserSession } from '../Utils/Common';
import {Container, FormWrap, Icon, FormContent, Form, FormH1, FormLabel, FormInput, FormButton} from './SigninElements'

const SignIn = (props) => {

    const [email, setEmail]= useState('');
    const [password, setPassword]= useState('');
    const [error, setError] = useState(null);
    const [loading, setLoading]= useState(false);

    const handelSubmit = (e) => {
        e.preventDefault();
        setError(null)
        setLoading(true)
        axios.post("/api/signin", {
            email: email,
            password: password
        }).then(response => {
            setLoading(false)
            setUserSession(response.data.token)
            props.history.push('/dashboard')
        }).catch(error => {
            setLoading(false);
            if(error.response.status === 404 || error.response.status === 400){
                setError(error.response.data.message)
            }
            else {
                setError("Somthing went wrong. Please try again later.");
            };
            console.error('error >>>>', error);
        })
    }
    return (
        <Container>
            <FormWrap>
                <Icon to="/">Solvency</Icon>
                <FormContent>
                    <Form onSubmit ={handelSubmit} autoComplete="off">
                        <FormH1>Sign in to your account</FormH1>
                        <FormLabel htmlFor='for'>Email</FormLabel>
                        <FormInput value={email} type='email' name='email' required  placeholder='Enter Your Email' onChange={e => setEmail(e.target.value)}/>
                        <FormLabel htmlFor='for'>Password</FormLabel>
                        <FormInput value={password} type='password' name='password' required  placeholder='Enter Your Password' onChange={e => setPassword(e.target.value)} />
                        {error && <p style={{color: 'red',textAlign: 'Center', paddingBottom: '10px'}}>{error}</p>}
                        <FormButton type='submit' value=
                            {
                            loading ?
                            <img src="https://media.giphy.com/media/kUTME7ABmhYg5J3psM/giphy.gif" alt="SignIn"/> :
                            "Login"
                            } 
                            disabled={loading} onSubmit={handelSubmit}>SignIn</FormButton>
                    </Form>
                </FormContent>
            </FormWrap>
        </Container>
    )
}


export default SignIn;
