import React from 'react';
import {withRouter} from 'react-router-dom';
import {Container, Table, TableRow, TableHeading, TableData} from './EmployeeElements'

const Employeedb = (props) => {
    const renderTable = ({orderdata}) => {
        if(orderdata){
            return orderdata.map((item) => {
                return(
                    <TableRow>
                        <TableData>{item.Emp_Id}</TableData>
                        <TableData>{item.Emp_Name}</TableData>
                        <TableData>{item.Designation}</TableData>
                        <TableData>{item.Emp_Phone}</TableData>
                        <TableData>{item.Emp_Email}</TableData>
                        <TableData>{item.Emp_Address}</TableData>
                    </TableRow>
                )
            })
        }
    }

    return(
    
        <Container style={{overflowX:'auto'}}>
            <center><h3>Employees Details</h3></center>
            <Table className="table table-responsive">
                <thead>
                    <TableRow>
                        <TableHeading>Emp_ID</TableHeading>
                        <TableHeading>Employee Name</TableHeading>
                        <TableHeading>Designation</TableHeading>
                        <TableHeading>Employee Phone</TableHeading>
                        <TableHeading>Employee Email</TableHeading>
                        <TableHeading>Employee Address</TableHeading>
                    </TableRow>
                </thead>
                <tbody>
                    {renderTable(props)}
                </tbody>
            </Table>
        </Container>
    )
}

export default withRouter(Employeedb);