import React from 'react';
import {IconContext} from 'react-icons/lib';
import { withRouter } from 'react-router-dom';
import {animateScroll as scroll} from 'react-scroll'
import { removeUserSession } from '../../Utils/Common'


import {Nav, NavbarContainer, NavLogo, NavBtn, NavBtnLink} from './NavbarElements'

const Header = ({toggle, history}) => {
    const handelLogoutClick=() =>{
        removeUserSession();
        history.push('/signin')
    }

    const toggleHome = () =>{
        scroll.scrollToTop();
    }

    return (
        <>
        <IconContext.Provider value={{color: '#fff'}}>
            <Nav>
                <NavbarContainer>
                    <NavLogo to='/dashboard' onClick={toggleHome}>Solvency</NavLogo>
                    <NavBtn>
                        <NavBtnLink onClick={handelLogoutClick} >Sign Out</NavBtnLink>
                    </NavBtn>
                </NavbarContainer>
            </Nav>
        </IconContext.Provider>
        </>
    )
}

export default withRouter (Header);
